.header-wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.header {
    background-color: rgb(53, 46, 63);
    
    background-image: url('../images/banner.png') ;
    background-size: cover ; 
    background-repeat: no-repeat;
    padding: 20px 0;
}

.pin_logo{
    width: 65px;
    height: 65px;
    display: flex;
}

.navigation-list {
    display: flex;
}

.navigation-list_item a {
    color: rgb(255, 255, 255);
    text-decoration: none;
    font-size: 20px;
}

.profile_img {
    width: 60px;
    height: 60px;
    display: flex;
}

.navigation-list_item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 30px;
}

